import { Component, OnInit } from "@angular/core";
import { AuthService } from "../shared/login/login.service";
import { NbToastrService } from "@nebular/theme";
import { UserInterface } from "../shared/login/login.interface";
import { Router, ActivatedRoute } from "@angular/router";
import { DataDashboardService } from "../../shared/Services/data-dashboard.service";

@Component({
  selector: "ngx-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.scss"],
})
export class LoginComponent implements OnInit {
  user: Partial<UserInterface> = {};

  constructor(
    private authService: AuthService,
    private toastr: NbToastrService,
    private dataDashboardService: DataDashboardService
  ) {}

  async ngOnInit(): Promise<void> {
    /* let user = []

    user.map(async (e) => {
      e.password = await this.authService.hashPass(e.password);
    }); */
    const data = await this.dataDashboardService.decodeJson();
    
  }

  async login() {
    try {
      if (this.user && this.user.userName && this.user.password) {
        const temp: boolean = await this.authService.login(this.user);
        if (temp) {
          this.toastr.success("Đăng nhập thành công!");
        } else {
          this.toastr.warning("Sai thông tin đăng nhập!");
        }
      } else {
        this.toastr.warning("Vui lòng nhập đầy đủ thông tin");
      }
    } catch (error) {
      this.toastr.warning("Sai thông tin đăng nhập!");
    }
  }
}
